/*global*/
html,
body {
  height: 100%;
}

img {
  max-width: 100%;
  object-fit: contain;
}

a {
  text-decoration: none;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

/*index.html*/
.page__body {
  font-family: "Arial", sans-serif;
  font-style: normal;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  color: #444444;
  margin: 0;
}
@media (min-width: 768px) {
  .page__body {
    font-size: 16px;
    line-height: 24px;
  }
}

.page__main--overflow {
  overflow: hidden;
}

.page-title {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
  color: #000000;
}
@media (min-width: 768px) {
  .page-title {
    font-size: 60px;
    line-height: 60px;
  }
}

.button {
  display: flex;
  justify-content: center;
  text-align: center;
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  text-transform: uppercase;
  width: 100%;
  background-color: transparent;
  color: #000000;
  margin: 0;
  border: none;
}
.button--primary {
  background-color: #68b738;
  color: #ffffff;
  font-size: 14px;
  line-height: 18px;
  padding: 11px;
}
.button--primary:hover, .button--primary:focus {
  background-color: #5eaa2f;
}
.button--primary:active {
  color: rgba(255, 255, 255, 0.3);
}
@media (min-width: 768px) {
  .button--primary {
    font-size: 20px;
    line-height: 20px;
    padding: 17px 11px;
  }
}
.button--secondary {
  background-color: #68b738;
  color: #ffffff;
  font-size: 16px;
  line-height: 20px;
  padding: 10px;
}
.button--secondary:hover, .button--secondary:focus {
  background-color: #5eaa2f;
}
.button--secondary:active {
  color: rgba(255, 255, 255, 0.3);
}

.map {
  height: 362px;
}
@media (min-width: 768px) {
  .map {
    min-height: 400px;
  }
}
@media (min-width: 1280px) {
  .map {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
  }
}

.map__view {
  width: 100%;
  height: 100%;
  border: 0;
}
.map__view--nojs {
  display: none;
}

.map__image {
  display: none;
  margin: 0 auto;
}
@media (min-width: 1280px) {
  .map__image {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
  }
}
.map__image--nojs {
  display: block;
}

.wrapper {
  max-width: 1280px;
  padding: 0 20px;
  margin: 0 auto;
}
@media (min-width: 768px) {
