.program-cards__item {
  position: relative;
  padding: 30px 20px 7px 20px;
  margin-bottom: 20px;
  background-color: #f2f2f2;
}
.program-cards__item--slim::before {
  content: "";
  position: absolute;
  top: 21px;
  left: 22px;
  width: 50px;
  height: 53px;
  background-image: url("../img/cat-slim.svg");
  background-size: cover;
}
.program-cards__item--pro::before {
  content: "";
  position: absolute;
  top: 24px;
  left: 13px;
  width: 67px;
  height: 50px;
  background-image: url("../img/cat-fat.svg");
  background-size: cover;
}
@media (min-width: 768px) {
  .program-cards__item {
    padding: 77px 315px 42px 52px;
    margin-bottom: 30px;
  }
  .program-cards__item--slim::before {
    top: 81px;
    right: 62px;
    left: auto;
    width: 200px;
    height: 211px;
  }
  .program-cards__item--pro::before {
    top: 91px;
    right: 28px;
    left: auto;
    width: 268px;
    height: 200px;
  }
}
@media (min-width: 1280px) {
  .program-cards__item {
    padding: 77px 52px 45px 51px;
    margin-bottom: 0;
  }
  .program-cards__item--slim::before {
    top: 41px;
    left: 52px;
    right: auto;
    width: 100px;
    height: 106px;
  }
  .program-cards__item--pro::before {
    top: 47px;
    left: 33px;
    right: auto;
    width: 134px;
    height: 100px;
  }
}
.program-cards__item:last-child {
  margin-bottom: 0;
}

.program-cards__title {
  margin: 0 0 29px 71px;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 24px;
  line-height: 37px;
  font-weight: 400;
  color: #000000;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .program-cards__title {
    font-size: 36px;
    line-height: 36px;
    margin-left: 0;
    margin-bottom: 25px;
    flex-grow: 1;
  }
}
@media (min-width: 1280px) {
  .program-cards__title {
    margin-left: 163px;
    margin-bottom: 74px;
  }
}

.program-cards__text {
  margin: 0 0 27px 0;
}
@media (min-width: 768px) {
  .program-cards__text {
    margin-bottom: 36px;
    color: #000000;
  }
}
@media (min-width: 1280px) {
  .program-cards__text {
    width: auto;
    margin-bottom: 11px;
  }
}

.program-cards__button {
  font-size: 16px;
  line-height: 16px;
  padding: 15px 0;
  justify-content: start;
  position: relative;
  align-items: center;
}
.program-cards__button span {
  margin-right: 15px;
}
.program-cards__button svg {
  flex-shrink: 0;
}
@media (max-width: 767px) {
  .program-cards__button::before {
    position: absolute;
    content: "";
    width: 100%;
    height: 1px;
    left: 0;
    top: -4px;
    background-color: #d9d9d9;
  }
}
.program-cards__button:hover svg {
  transform: scaleX(1.3);
}
.program-cards__button:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .program-cards__button {
    font-size: 20px;
    line-height: 30px;
    align-self: flex-end;
  }
  .program-cards__button span {
    margin-right: 25px;
  }
}
@media (min-width: 768px) {
  .program-cards__button span {
    margin-right: 28px;
  }
}

.main-nav {
  width: 100%;
}
@media (min-width: 768px) {
  .main-nav {
    width: auto;
    display: flex;
    flex-wrap: wrap;
  }
}

.header {
  min-width: 280px;
}
@media (max-width: 767px) {
  .header--inner {
    outline: 1px solid #d9d9d9;
  }
}
@media (min-width: 1280px) {
  .header--index::before {
    content: "";
    position: absolute;
    right: 0;
    width: 50%;
    height: 694px;
    background-image: url("../img/bg_promo_cat_desk.jpg");
    background-repeat: no-repeat;
    background-position: right 0 top;
    background-color: #62a63b;
    z-index: -1;
  }
}
@media (min-width: 1280px) and (min-resolution: 2dppx) {
  .header--index::before {
    background-image: url("../img/bg_promo_cat_desk@2x.jpg");
    background-size: 720px auto;
  }
}
.header--menu-closed .header__toggle::before {
  content: "";
  position: absolute;
  top: 22px;
  right: 20px;
  width: 24px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}
.header--menu-opened .header__toggle::before, .header--menu-opened .header__toggle::after {
  content: "";
  position: absolute;
  top: 30px;
  right: 20px;
  width: 24px;
  height: 2px;
  background-color: #000000;
}
.header--menu-opened .header__toggle::before {
  transform: rotate(45deg);
}
.header--menu-opened .header__toggle::after {
  transform: rotate(-45deg);
}
@media (max-width: 767px) {
  .header--menu-closed .header__nav {
    display: none;
  }
}
@media (max-width: 767px) {
  .header--menu-opened .header__nav {
    display: block;
  }
}
.header--menu-nojs .header-logo {
  display: block;
  margin-bottom: 12px;
}
.header--menu-nojs .header__nav-list {
  position: static;
  min-height: 0;
}
.header--menu-nojs .header__toggle {
  display: none;
}
@media (max-width: 767px) {
  .header--menu-nojs .menu__item {
    width: 100vw;
    margin-left: -20px;
  }
}
.header--menu-nojs .nav-list {
  display: block;
}

