.work__item {
  position: relative;
  display: flex;
  min-height: 60px;
  margin-bottom: 20px;
}
@media (min-width: 768px) {
  .work__item {
    padding: 142px 0 57px 0;
    margin-bottom: 0;
    min-height: 280px;
  }
  .work__item::after {
    content: counter(num);
    position: absolute;
    counter-increment: num;
    font-family: "Oswald", "Arial", sans-serif;
    font-size: 280px;
    line-height: 280px;
    font-weight: 400;
    color: #f2f2f2;
    z-index: -10;
    left: 113px;
    top: -9px;
  }
  .work__item:first-child::after {
    left: 118px;
  }
}
.work__item:nth-child(n)::before {
  content: "";
  display: block;
  width: 60px;
  height: 60px;
  background-repeat: no-repeat;
  background-size: contain;
  flex-shrink: 0;
  margin-right: 20px;
}
@media (min-width: 768px) {
  .work__item:nth-child(n)::before {
    position: absolute;
    width: 80px;
    height: 80px;
    top: 31px;
  }
}
.work__item:nth-child(1)::before {
  background-image: url("../img/work-1_leaf.png");
}
.work__item:nth-child(2)::before {
  background-image: url("../img/work-2_powder.png");
}
.work__item:nth-child(3)::before {
